@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Preahvihear&display=swap);
.avatar__container{
    display: grid;
    grid-template-columns: 1fr;
    padding: 2rem;
}

.avatar__content{
    display: flex;
    flex-wrap: nowrap;
    width: auto;
    grid-gap: 1rem;
    gap: 1rem;
}

.avatar__image{
    padding: .2rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

.avatar__image img{
    border-radius: 100%;
    width: 80px;
    aspect-ratio: 1/1;
    background-color: var(--first-color);
}

.avatar__data{
    display: grid;
    place-items: center;
    background: linear-gradient(48deg,var(--first-color)30%, var(--second-color)90%);
    -webkit-background-clip: text;
    color: transparent;
}

.avatar__icon{
    color: var(--first-color);
}






.product__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 2rem;
    gap: 2rem;
    padding: 2rem;
}

.product__catergory{
    padding: 2rem 2rem 0;
    background: linear-gradient(68deg,var(--first-color)40%, var(--second-color)50%);
    -webkit-background-clip: text;
    color: transparent;
    font-family: 'Preahvihear', sans-serif;
}

.product__card{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    background: transparent;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 1rem;
}

.product__image{
    display: grid;
    place-items: center;
}

.product__spec{
    display: grid;
    place-items: center;
}

.product__spec-wrapper{
    line-height: 2;
}

.product__spec-wrapper > p{
    margin-bottom: .5rem;
}

.buy__now{
    margin: auto;
    width: 100px;
    font-size: .9rem;
    background-color: transparent;
    color: var(--text-color);
    background: linear-gradient(45deg,var(--first-color),var(--second-color));
    border-radius: .2rem;
    display: block;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}


/* ======================== tablets ======================== */
@media screen and (max-width: 1024px){
    .product__container{
        grid-template-columns: 1fr 1fr;
    }
}

/* ======================== small-devices ======================== */
@media screen and (max-width: 600px){
    .product__container{
        grid-template-columns: 1fr;
    }
}
.testimonial__title{
  margin: auto 2rem;
}

.image__container{
  margin: 1rem 2rem;
}

.image__wrapper{
  display: grid;
  place-items: center;
  object-fit: cover;
  overflow: hidden;
}

.image__wrapper img{
  border-radius: 1rem;
  height: 70vh;
}

.swiper-pagination-bullet-active {
  background-color: var(--first-color) !important;
}


/* ======================== tablets ======================== */
@media screen and (max-width: 1024px){
  .image__wrapper img{
    height: 50vh;
  }
}

/* ======================== small-devices ======================== */
@media screen and (max-width: 600px){
  .image__wrapper img{
    height: 40vh;
  }
}
  
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --background-color: #fff;
    --background-color-variant: rgba(255,255,255,0.);
    --first-color: hsl(203, 71%, 60%);
    --second-color: hsl(277, 56%, 68%);
    --text-color: black;
    --text-color-light: hsl(203, 8%, 80%);
    --body-color: hsl(259, 20%, 18%);
    --transition: all 400ms ease;
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html{
    scroll-behavior: smooth;
}


::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background-color: #fff;
    background-color: var(--background-color);
    color: black;
    color: var(--text-color);
    line-height: 1.7;
}


/* ======================== general style ======================== */
.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
    background: linear-gradient(90deg, hsl(277, 56%, 68%) 30%, hsl(203, 71%, 60%) 70%);
    background: linear-gradient(90deg, var(--second-color) 30%, var(--first-color) 70%);
    -webkit-background-clip: text;
    color: transparent;
}

section{
    margin-top: 8rem;
}

section > h2, section > h5{
    text-align: center;
    color: hsl(203, 8%, 80%);
    color: var(--text-color-light)
}

section > h2{
    background: linear-gradient(90deg, hsl(277, 56%, 68%) 30%, hsl(203, 71%, 60%) 70%);
    background: linear-gradient(90deg, var(--second-color) 30%, var(--first-color) 70%);
    -webkit-background-clip: text;
    color: transparent;
    margin-bottom: 3rem;
}

.text-light{
    color: hsl(203, 8%, 80%);
    color: var(--text-color-light);
}

a{
    color: hsl(203, 71%, 60%);
    color: var(--first-color);  
    transition: all 400ms ease;  
    transition: var(--transition);
}

a:hover{
    color: hsl(203, 8%, 80%);
    color: var(--text-color-light);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: hsl(203, 71%, 60%);
    color: var(--first-color);
    padding: .75rem 1.2rem;
    border-radius: .4rem;
    border: 1px solid hsl(203, 71%, 60%);
    border: 1px solid var(--first-color);
    transition: all 400ms ease;
    transition: var(--transition);
    cursor: pointer;
}

.btn:hover{
    background: hsl(203, 8%, 80%);
    background: var(--text-color-light);
    color: #fff;
    color: var(--background-color);
    border-color: transparent;
}

.btn-primary{
    background: hsl(203, 71%, 60%);
    background: var(--first-color);
    color: #fff;
    color: var(--background-color);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ======================== tablets ======================== */
@media screen and (max-width: 1024px){
    .container{
        width: 86%;
        width: var(--container-width-md);
    }

    section{
        margin-top: 6rem;
    }
}

/* ======================== small-devices ======================== */
@media screen and (max-width: 600px){
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}


